.buttons{
  display: inline-block;
  padding: 5px 4px;
  font-size: 15px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: cornflowerblue;
  border: none;
  border-radius: 1px;
  box-shadow: 0 3px darkblue;}

.buttons:hover {background-color: cornflowerblue}

.buttons:active {
  background-color: cornflowerblue;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}